/* inspired by https://www.florin-pop.com/blog/2019/04/how-to-create-a-timeline-with-react/ and tried to make it responsive */

/* creating line part of timeline */
.timeline-container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 40px 0;
}

.timeline-container::after {
    background-color: #343a40;
    content: '';
    position: absolute;
    left: calc(50% - 2px);
    width: 4px;
    height: 100%;
}
/* moves timeline to right side of page when on mobile */
@media (max-width: 500px) {
  .timeline-container::after{
    left: calc(80% - 2px);
  }
}

.timeline-item {
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
    position: relative;
    margin: 10px 0;
    width: 50%;
}
/* makes boxes larger on mobile */
@media(max-width: 500px){
  .timeline-item{
      width: 70%;
  }
}
/* pushes all content to one side on mobile */
@media (min-width: 600px) {

  .timeline-item:nth-child(odd) {
      align-self: flex-end;
      justify-content: flex-start;
      padding-left: 30px;
      padding-right: 0;
  }
  .timeline-item:nth-child(odd) .timeline-item-content {
      text-align: left;
      align-items: flex-start;
  }

  .timeline-item:nth-child(odd) .timeline-item-content::after {
      right: auto;
      left: -7.5px;
      box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);

  }
  .timeline-item:nth-child(odd) .timeline-item-content .circle {
      right: auto;
      left: -40px;
  }

}



.timeline-item-content {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 15px;
    position: relative;
    width: 400px;
    max-width: 70%;
    text-align: right;
}

.timeline-item-content::after {
    content: ' ';
    background-color: #fff;
    box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
    position: absolute;
    right: -7.5px;
    top: calc(50% - 7.5px);
    transform: rotate(45deg);
    width: 15px;
    height: 15px;
}




.timeline-item-content time {
    color: #777;
    font-size: 1.5em;
    font-weight: bold;
}

.timeline-item-content p {
    font-size: 16px;
    line-height: 24px;
    margin: 15px 0;
    max-width: 300px;
}

.timeline-item-content a {
    font-size: 14px;
    font-weight: bold;
}

.timeline-item-content a::after {
    content: ' â–º';
    font-size: 12px;
}

.timeline-item-content .circle {
    background-color: #fff;
    border: 3px solid #343a40;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 10px);
    right: -40px;
    width: 20px;
    height: 20px;
    z-index: 100;
}

/* moves circle to area where timeline is, having a bit of trouble with getting this just right */
@media (max-width: 500px) {
  .timeline-item-content .circle{

    left: calc(135%);
  }
}
